@import "src/styles/global.scss";
.modal {
  position: fixed;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 100px;
  min-width: 100vw;
  min-height: 100vh;
  background-color: rgba(33, 9, 1, 0.3);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  inset: 0;

  &__wrapper {
    overflow-y: auto;
    border-radius: 4px;
    width: 100%;
    max-width: 850px;
    max-height: 90vh;
    line-height: 120%;
    background-color: $color-bg-accent;

    &::-webkit-scrollbar {
      width: 10px;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 4px;
      background-color: $color-primary;
    }
  }
}
